.knob__input::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: transparent;
  width: 50px;
  height: 50px;
  border: none;
}

.knob__input::-webkit-slider-runnable-track {
  -webkit-appearance: none;
}

.knob__input::-moz-range-thumb {
  -webkit-appearance: none;
  background: transparent;
  width: 50px;
  height: 50px;
  border: none;
}

.knob__input {
  -webkit-appearance: none;
  background: transparent;
  width: 300px;
  height: 50px;
  -ms-touch-action: pan-x pinch-zoom;
      touch-action: pan-x pinch-zoom;
  border: none;
  outline: none;
}

.knob__container {
  display: inline-block;
  -webkit-transform: rotate(270deg);
      -ms-transform: rotate(270deg);
          transform: rotate(270deg);
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  width: 50px;
  height: 50px;
  position: relative;
}

.knob__spinner {
  z-index: -1;
  background-color: #777;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 75px;
}

.knob__marker {
  background-color: white;
  width: 4px;
  height: 50%;
  margin: 0 auto;
}



